import Box from "@mui/material/Box";
import { DeviceFrameset } from "react-device-frameset"

import 'react-device-frameset/styles/marvel-devices.min.css'
import PacmanLoader from "react-spinners/PacmanLoader";
import { useAppSelector } from "../../redux/hooks";
import { selectChatter } from "../../features/chatter/chatter";
import { AppData, TabType, isJson, parseAppData } from "../../utils/utils";
import React, { useEffect, useState } from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";

import Screen from './Screen'

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box>
          {children}
        </Box>
      )}
    </div>
  );
}

const SimualtorContent = () => {

  const chatter = useAppSelector(selectChatter)

  const [value, setValue] = useState(0);
  const [appData, setAppData] = useState<AppData | null>(null)

  useEffect(() => {
    setAppData(parseAppData(chatter.data))
  }, [chatter.data])

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return <Box sx={{ width: '100%' }}>
    {
      appData && appData?.tabs.map((item, index) => {
        if (item.type === TabType.modular)
          item.name = appData.name;
        return <TabPanel value={value} index={index} key={`tab_${index}`}>
          <Screen tab={item} />
        </TabPanel>
      })
    }
    <Tabs value={value} onChange={handleChange}
      scrollButtons="auto"
      allowScrollButtonsMobile centered variant="fullWidth">
      {
        appData && appData.tabs.map((tabItem, index) => {
          return <Tab label={tabItem.type} key={`label_${index}`} />
        })
      }
    </Tabs>
  </Box>
}

const Simulator = () => {

  const chatter = useAppSelector(selectChatter)

  let isLoading = !isJson(chatter.data);

  return <DeviceFrameset device="iPhone X" color="gold" zoom={0.7}>
    <Box
      display='flex'
      flex='1'
      height='100%'
      justifyContent='space-around'
      alignItems='center'
      sx={{
        backgroundColor: 'background.paper',
      }}
    >
      {isLoading ? <PacmanLoader color="#36d7b7" /> : <SimualtorContent />}
    </Box>
  </DeviceFrameset>
}

export default Simulator;