import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from 'axios';
import { MessageType } from "./chatter";
import { TypeContextString } from "../../utils/utils";

export const requestGPT = createAsyncThunk('chatter/requestGPT', async (payload: { API_KEY: string, input: string }, { rejectWithValue }) => {
  try {

    const { API_KEY, input } = payload;
    const { data } = await axios.post(
      "https://api.openai.com/v1/chat/completions",
      {
        "model": "gpt-3.5-turbo",
        "messages": [{ "role": "user", "content": input + '\n\n please use below as code context in typescript' + TypeContextString + '\n\n and response in json format.' }]
      },
      {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${API_KEY}`,
        },
      }
    );
      console.log('data:',data)
    if (data.choices) {
      const firstChoice = data.choices[0] as { message: { role: String, content: string } }
      return {content:firstChoice.message.content,type:MessageType.in}
    }

    return null;
  } catch (error) {
    return rejectWithValue(error);
  }
})