import Box from "@mui/material/Box"
import Chip from "@mui/material/Chip";
import { Message, MessageType } from "../../features/chatter/chatter";

interface MessageItemProps {
  message: Message
}

const MessageItem = (props: MessageItemProps) => {
  const { message } = props;
  const bgColor = message.type === MessageType.in? '#c666a0':'#4189ed'
  return <Box display='flex' justifyContent={message.type === MessageType.in?'flex-start':'flex-end'}>
    <Chip label={message.content} sx={{backgroundColor:bgColor,    height: 'auto',
    '& .MuiChip-label': {
      display: 'block',
      whiteSpace: 'normal',
    },paddingTop:1,paddingBottom:1,marginBottom:2,maxWidth:'90%'}} />
  </Box>
}

export default MessageItem