import Box from "@mui/material/Box";
import List from "@mui/material/List";
import { Key, useEffect, useRef, useState } from "react";
import MessageItem from "./MessageItem";
import TextField from "@mui/material/TextField";
import { Button } from "@mui/material";
import { useAppDispatch, useAppSelector } from "../../redux/hooks";
import { Message, selectChatter } from "../../features/chatter/chatter";
import { requestGPT } from "../../features/chatter/requestGPT";

const ChatBox = () => {

  const dispatch = useAppDispatch()
  const { APIKey, messages,isLoading, } = useAppSelector(selectChatter);

  const bottomRef = useRef<HTMLDivElement>(null);
  const [currentMessage, setCurrentMessage] = useState('')

  useEffect(() => {
    setTimeout(() => {
      bottomRef.current?.scrollIntoView({ behavior: 'smooth', });
    }, 100);
  }, [messages])

  const handleSendMessage = () => {
    dispatch(requestGPT({ API_KEY: APIKey, input: currentMessage }))
    setCurrentMessage('')
  }

  const disableSendBtn = isLoading || currentMessage.length < 2;

  return <Box
    sx={{ overflow: 'hidden', width: 300}}>
    <List style={{ paddingBottom: 20, height: 400, overflow: 'auto' }}>
      {messages.map((item: Message, index: { toString: () => Key | null | undefined; }) => <MessageItem
        message={item} key={index.toString()} />)}
      <div ref={bottomRef} />
    </List>
    <Box>
      <TextField
        id="filled-multiline-static"
        label="New message"
        multiline
        rows={2}
        placeholder="type your thinking here"
        fullWidth
        value={currentMessage}
        onChange={event => setCurrentMessage(event.target.value)}
      />
      <Button fullWidth onClick={handleSendMessage} disabled={disableSendBtn}>{isLoading?'Loading...':'Send!'}</Button>
    </Box>
  </Box>
}

export default ChatBox;