import Typography from "@mui/material/Typography";
import { Tab, TabType, capitalizeFirstLetter } from "../../utils/utils";
import Skeleton from "@mui/material/Skeleton";
import Box from "@mui/material/Box";
import Stack from "@mui/material/Stack";
import Grid from "@mui/material/Grid";

const MockContent = (props: { tab: Tab }) => {

  switch (props.tab.type) {
    case TabType.library:
      return <Box>
        {Array.from(Array(5)).map((_, index) =>
          <Skeleton animation={false} variant="rectangular" sx={{ margin: 'auto', marginTop: 2 }} width={350} height={118} key={`mock_modular_${index}`} />
        )}
      </Box>

    case TabType.modular:
      return <Grid container spacing={1}>
        {Array.from(Array(12)).map((_, index) =>
          <Grid item xs={4} key={`mock_modular_${index}`}>
            <Skeleton animation={false} variant="rectangular" width={120} height={118} />
          </Grid>
        )}
      </Grid>

    case TabType.profile:
      return <Box>
        <Stack spacing={1}>
          <Skeleton animation={false} variant="text" sx={{ fontSize: '1rem' }} />
          <Skeleton animation={false} variant="circular" width={40} height={40} />
          <Skeleton animation={false} variant="rectangular" width={210} height={60} />
          <Skeleton animation={false} variant="rounded" width={210} height={60} />
        </Stack>
      </Box>
    case TabType.website:
      // eslint-disable-next-line jsx-a11y/iframe-has-title
      return <Box><iframe style={{width:372,height:700}} src="https://iframetester.com/"></iframe></Box>
    default:
      return <Typography variant="h6" sx={{ color: '#ff0', flexGrow: 1, marginTop: 10, textAlign: 'center' }}>
        {capitalizeFirstLetter(props.tab.type)}12345
      </Typography>
  }
}


export default MockContent;