import './App.css';
import { useAppSelector } from './redux/hooks';
import APIInputer from './components/APIInputer';
import createTheme from '@mui/material/styles/createTheme';
import ThemeProvider from '@mui/material/styles/ThemeProvider';
import Box from '@mui/material/Box';
import Simulator from './components/Simulator/Simulator';
import ChatBox from './components/ChatBox';
import { selectChatter } from './features/chatter/chatter';


const Content = () => {
  const chatter = useAppSelector(selectChatter)

  if (chatter.APIKey && chatter.APIKey.length) {
    return <Box sx={{ display: 'flex', alignContent: 'center', alignItems: 'center', overflow: 'hidden' }}>
      <Simulator />
      <ChatBox />
    </Box>
  }

  return <APIInputer />
}

const darkTheme = createTheme({
  palette: {
    mode: 'dark',
  },
});

function App() {
  return (
    <ThemeProvider theme={darkTheme}>
      <Box
        display='flex'
        flex='1'
        justifyContent='center'
        sx={{
          backgroundColor: 'background.paper',
          overflow: 'hidden',
        }}>
        <Content />
      </Box>
    </ThemeProvider>

  );
}

export default App;
