import { useAppDispatch } from "../redux/hooks"
import Box from "@mui/material/Box"
import { useState } from "react"
import Button from "@mui/material/Button"
import FormControl from "@mui/material/FormControl"
import InputLabel from "@mui/material/InputLabel"
import Input from "@mui/material/Input"
import { K_API_KEY_MIN_LENGTH } from "../common"
import { setAPIKey } from "../features/chatter/chatter"

const APIInputer = () => {

  const dispatch = useAppDispatch()

  const [apiKey, setApiKey] = useState('')

  return <Box minWidth={400} minHeight={'100vh'}>
    <FormControl
      fullWidth sx={{ m: 1 }}
      variant="standard"
    >
      <InputLabel htmlFor="standard-adornment-amount">Plase input your ChatGPT API KEY</InputLabel>
      <Input
        type="password"
        id="standard-adornment-amount"
        onChange={event => setApiKey(event.target.value)}
      />
      <Button type="submit" disabled={apiKey.length < K_API_KEY_MIN_LENGTH} onClick={() => dispatch(setAPIKey(apiKey))}>Confirm</Button>
    </FormControl>
  </Box>
}
export default APIInputer