import { PayloadAction, createSlice } from "@reduxjs/toolkit"
import { RootState } from "../../redux/store";
import { K_API_KEY_MIN_LENGTH } from "../../common";
import { requestGPT } from "./requestGPT";
import { isJson } from "../../utils/utils";

export enum MessageType {
  in = 'in',
  out = 'out'
}

export interface Message {
  content: string;
  type: MessageType;
  json?:string;
}

interface ChatterState {
  APIKey: string,
  messages: Message[],
  data: any,
  isLoading: boolean,
  isSuccess: boolean,
  errorMessage: any
}

const initialState: ChatterState = {
  APIKey: '',
  messages: [{ content: 'Hi, there', type: MessageType.in }],
  data: "",
  isLoading: false,
  isSuccess: false,
  errorMessage: '',
}

export const chatterSlice = createSlice({
  name: 'chatter',
  initialState,
  reducers: {
    setAPIKey: (state, action: PayloadAction<string>) => {
      if (action.payload.length >= K_API_KEY_MIN_LENGTH)
        state.APIKey = action.payload;
    }
  },
  extraReducers: (builder) => {
    builder.addCase(requestGPT.pending, (state, action) => {
      state.isLoading = true;
      state.messages.push({content:action.meta.arg.input,type:MessageType.out})
    })
    builder.addCase(requestGPT.fulfilled, (state, action) => {
      state.isLoading = false;
      state.isSuccess = true;

      let msg = {content:action.payload?.content || '',type:MessageType.in} as Message;
      if (isJson(msg.content)) {
        msg.json =  msg.content;
        msg.content = 'APP updated!';
        state.data = msg.json;
      }

      state.messages.push(msg)
    })

    builder.addCase(requestGPT.rejected, (state, action) => {
      state.isLoading = false;
      state.isSuccess = false;
      state.errorMessage = action
    })
  }
})

export const { setAPIKey } = chatterSlice.actions

export const selectChatter = (state: RootState) => state.chatter

export default chatterSlice.reducer