export function isJson(str: string) {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}
export function capitalizeFirstLetter(str: string) {
  return str.charAt(0).toUpperCase() + str.slice(1);
}

export enum TabType {
  modular = 'modular',
  website = 'website',
  profile = 'profile',
  library = 'library'
}

export interface Tab {
  type: TabType;
  name?: string;
}

export interface AppData {
  name: string;
  tabs: Tab[]
}

export const parseAppData = (jsonData: string): AppData => {
  if (isJson(jsonData)) {
    return JSON.parse(jsonData) as AppData;
  }
  return { name: 'empty', tabs: [] }
}

